.controls {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.search-bar {
 padding: 10px;
    border-radius: 12px;
    background: transparent;
    border: 1px solid white;
    height: fit-content;
    font-size: 1em;
}
.search-input{
  background: transparent;
  border: none;
}
select {
  padding: 1rem;
    border-radius: 5px;
    background: #6200ea;
    height: fit-content;
    color: #fff;
    border: none;
}

.UpdateProductList {
  padding: 1rem;
  border: none;
  border-radius: 5px;
  background-color: #6200ea;
  cursor: pointer;
  margin-top: 0px;
}

.UpdateProductList:hover {
  background-color: #3c009d;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

h2 {
  margin-bottom: 20px;
  color: white;
}

.product-container
{
  margin: 1rem;
  padding: 1rem;
}

::placeholder
{
  color: #fff;
}

.search-bar
{
  width: 40%;
  margin-bottom: 4rem;
} 

.AddProducts {
  padding: 1rem;
  border: none;
  border-radius: 5px;
  background-color: #6200ea;
  cursor: pointer;
  margin-top: 0px;
}